<template>
    <div id="contact">
        <h2>Contact</h2>
        <p v-if="loloh" class="success">Your message is ready please press submit</p>
        <p v-if="!loloh" class="danger">Please type the appropriate email and message</p>
        <p v-if="!loloh" class="danger">Inputs can't be less than 10</p>
        <form action="post">
        <input type="email" class="rounded-3" placeholder="E-mail" v-model="email" @keypress.enter.prevent="emailSubmit" @keyup="validation">
        <textarea name="textarea" id="ta" class="rounded-3" cols="50" rows="10" placeholder="your message" v-model="message" @keyup="validation" @keypress.enter.prevent="emailSubmit"></textarea>
        <button class="btn  rounded-pill mt-3 px-4 py-3 fs-2 fw-bolder subs btn-primary" @click.prevent="emailSubmit" :disabled="!loloh">Submit</button>
        </form>
    </div>
</template>
<script>
 import axios from 'axios'

export default {
  data () {
    return {
      loloh: false,
      email: '',
      message: ''
    }
  },
  // logo_art99@gmail.com
  methods: {
    emailSubmit () {
      if(this.loloh){
      axios.post('https://hamoudamailer.herokuapp.com', { cusEmail: this.email,message:this.message,email:"logoexpert9898@gmail.com" }).then(function (response) {
        if (response.status === 200) {
          console.log('if is working')
        } else {
          console.log('if is not working')
        }
      })
        .catch(function (error) {
          console.log(error)
        })
      }
    },
    validation(){
      if(this.email.length < 10 && this.message.length < 10){
        this.loloh = false
      }
      if(this.email.length > 10 && this.message.length > 10){
        this.loloh = true
      }
      console.log(this.loloh)
    }
  }
}
</script>
<style lang="scss" scoped>
h2{
text-align: center;
color: white;
margin-top:3%;
margin-bottom:3%;
}
div{
    background-color: black;
    margin-top:3%;
    padding:10%;
}
form{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    input{
        width:40%;
        height:40px;
        margin:3%;
        }
    input::placeholder{
        color:rgb(44, 44, 44);
    }
}
.success{
  color:greenyellow;
  text-align:center;
  font-size:20px;
  font-weight:600;
}
.danger{
  color:rgb(252, 81, 81);
  text-align:center;
  font-size:20px;
  font-weight:600;
}
.subs{
  font-weight:bolder;
  font-size:1.2rem;
}
@media only screen and (max-width: 600px) {
form{
  input{
    width:70%;
  }
  textarea{
    width:85%;
  }
}
}
</style>
